.TodoItem {
  align-items: center;
  background-color: #fafafa;
  box-shadow: 0 5px 50px rgba(32, 35, 41, 0.15);
  display: flex;
  justify-content: center;
  margin-top: 24px;
  position: relative;
}

.TodoItem-p {
  font-size: 18px;
  font-weight: 400;
  line-height: 24px;
  margin: 24px 0 24px 24px;
  width: calc(100% - 120px);
}

.TodoItem-p--complete {
  text-decoration: line-through;
}
